.navbar {
  height: 70px;
  border-left: 0.75px solid rgb(231, 228, 288);
  display: flex;
  align-items: center;
  font-size: 14px;
  color: grey;
}

.wrapper {
  width: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search {
  display: flex;
  align-items: center;
  border: 0.5px solid lightgray;
  padding: 3px;
}

.inputNavbarAdmin {
  border: none;
  outline: none;
  background-color: transparent;
}

.inputNavbarAdmin::placeholder {
  font-size: 12px;
}

.items {
  display: flex;
  align-items: center;
}

.item {
  display: flex;
  align-items: center;
  margin-right: 20px;
  position: relative;
}

.icon {
  font-size: 20px;
}

.avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.counter {
  width: 15px;
  height: 15px;
  background-color: yellow;
  border-radius: 50%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  top: -5px;
  right: -5px;
}

.hrNavbarAdmin {
  height: 0;
  border: 0.5px solid lightgray;
  position: relative;
  z-index: 20;
}